import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CreditTransactionsComponent } from './credit-transactions/credit-transactions.component';
import { CashTransactionsComponent } from './cash-transactions/cash-transactions.component';

const routes: Routes = [
      {
        path: 'cash',
        component: CashTransactionsComponent
      },
      {
        path: 'credit',
        component: CreditTransactionsComponent
      },
      {
        path: '',
        redirectTo: 'cash',
        pathMatch: 'full'
      }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TransactionsRoutingModule { }
